/* Ensure body and html take full height */
html, body {
  height: 100%;
  padding: 0;
  font-family: "Lato", serif;
  background-color: black; /* Black background for the entire page */
}

section {
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100vh;
  width: 100vw;
  position: relative;
}
.container {
  position: relative;
  overflow: hidden;
}

canvas {
  position: absolute; /* Ensure canvas stays behind the content - Title backgorund */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  overflow: visible;
}

.hero {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  z-index: 0;
  overflow: visible; /* Ensure the canvas and light can extend beyond */
}

.hero .hero-content {
  position: absolute;
  top: 50%;
  left: 5vw;
  transform: translateY(-50%);
  z-index: 1;
  color: white;
  text-align: left;
  transition: transform 0.5s ease, opacity 0.5s ease; /* Smooth animation for content */
  
}

.first-line {
  font-size: 8em;
  margin-bottom: 0;
  position: relative;
  bottom: 30px; /* Adjust this value to move up/down */
  left:1%;
  font-family: "Libre Caslon Display", serif;
  font-weight: 400;
  font-style: normal;
}

.second-line {
  font-size: 7em;
  margin-bottom: 0;
  position: relative;
  bottom: 150px; /* Adjust this value to move up/down */
  left: 1%;
  font-family: "Lato", serif;
  font-weight: 900;
  font-style: normal;
}

.subheading {
  font-size: 20px;
  line-height: 1.5; /* Ensures proper spacing between lines */
  width: 100%; /* Adjust width so the text doesn't stretch across the whole page */
  margin-bottom: 0;
  position: relative;
  bottom: 170px; /* Adjust this value to move up/down */
  left: 2%;
  font-family: "Lato", serif;
  font-weight: 400;
  font-style: normal;
}

.scroll-icon {
  font-size: 40px; /* Size of the icon */
  color: #fff; /* White color for the icon */
  display: block; /* Make the icon a block element */
  margin: 20px auto; /* Center the icon */
  transition: transform 0.3s ease-in-out;
}

/* Optional: Add hover effect */
.scroll-icon:hover {
  transform: translateY(5px); /* Slightly move the icon down on hover */
  cursor: pointer;
}

  /*Hero section divider*/
  .section-divider-hero {
    height: 50vh; /* Half of the viewport height */
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center content vertically */
    align-items: flex-start; /* Align content to the left */
    padding-left: 5vw; /* Space from the left edge */
    overflow: visible; /* Ensure the canvas and light can extend beyond */
  }
  
  .section-line-hero {
    font-size: 5rem; /* Large text */
    font-weight: bold;
    color: #ffffff;
    text-transform: uppercase;
    margin: 0; /* Remove extra margin */
    line-height: 1.1; /* Reduce space between lines */
  }
  
  .section-subline-hero {
    font-size: 1.8rem; /* Slightly smaller */
    font-weight: 400;
    color: #bbbbbb;
    margin-top: 10px; /* Small gap between main text and instruction */
  }
  

